@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  font-family: "Roboto", sans-serif;
}

.contentContainer {
  margin-left: 20vw;
  animation-name: fadeIn;
  animation-duration: 2s;
}

.title {
  text-align: center;
  font-family: Georgia, "Times New Roman", Times, serif;
  margin-left: 1vw;
  font-size: 2vw;
}

h4 {
  font-size: 1.5vw;
  font-weight: bold;
}

.locationDates {
  font-weight: normal;
}

.description {
  margin-left: 3vw;
  font-size: 1vw;
  font-weight: normal;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
